import React, { Fragment } from 'react';
import { graphql } from 'gatsby';

import Theme from '../models/Theme';
import Drama from '../models/Drama';
import generatePinkx from '../utils/code';
import dramaDefault from '../utils/drama';
import { Screen, SEO } from '../components';

interface Props {
  data: {
    themeYaml: Theme;
  };
  pathContext: any;
}

interface States {
  drama: Drama;
  theme: Theme;
}

export default class ThemeTemplate extends React.PureComponent<Props, States> {
  constructor(props: any) {
    super(props);
    let drama: Drama = dramaDefault;
    let theme: Theme = this.props.data.themeYaml;
    if (typeof window !== `undefined`) {
      const dramaJson: any = window.localStorage.getItem('drama');
      const themeJson: any = window.localStorage.getItem('theme');
      if (dramaJson) {
        drama = JSON.parse(dramaJson);
      }
      if (themeJson) {
        theme = JSON.parse(themeJson);
      }
    }
    this.state = {
      drama,
      theme,
    };
  }

  // 生成html代码
  previewCode(drama: Drama, theme: Theme) {
    return `
<!DOCTYPE html><html><head><meta charset="utf-8" />
<meta http-equiv="Content-Type" content="text/html; charset=gb2312"><title>PinkX - 小粉红模拟器</title>
<link rel="stylesheet" type="text/css" media="screen" href="/demo/jjwxc.min.css" /></head><body bgcolor="#FFE7F7" topmargin="0">
<section class="top"><div class="google-ad">广告区</div></section><section class="boardname">
<div class="left">面包屑导航</div><div class="right">登录信息</div></section>
<table width="760" border="0" align="center" cellspacing="0" style="table-layout:fixed;word-wrap:break-word;">
<tbody><tr><td bgcolor="#E8F3FF"><div id="msgsubject" style="float:left;">主题：小粉红模拟器
<font color="#999999" size="-1">[1]</font></div><div style="float:right;">
<span style="font-weight:bold;font-size:14px;color:blue;cursor:pointer;display:inline;" id="addfavorite" onclick="addfavorite(2, 5068113)">收藏该帖</span></div></td></tr>
<tr class="comment_5068113"><td><table width="100%" border="0" cellspacing="0" cellpadding="0">
<tbody><tr><td valign="bottom" width="350"><div class="image1" style="display:inline-block;"></div>
<br></td><td class="textbook"><div align="right"><img class="heartimg" src="/demo/img/heart.gif" width="10" height="9">
<a href="#"><font color="#0d7328" size="-1">【出版书广告区】</font></a></div></td></tr></tbody>
</table></td></tr><tr class="comment_5068113 gifffter"><td>
<table width="760" border="0" cellpadding="0" cellspacing="0" style="table-layout:fixed;word-wrap:break-word;">
<tbody><tr><td class="read"><div id="topic">
${generatePinkx(drama, theme)}
</div></td></tr></tbody></table></td></tr><tr class="comment_5068113"><td class="authorname">
<div align="left"><font color="99CC00" size="-1">№0 </font><font color="99CC00">☆☆☆</font>PINKX
<font color="#999999">78adf6e4</font><font color="99CC00">于</font>2019-02-29 11:11:11留言
<font color="99CC00">☆☆☆</font></div></td></tr><tr class="comment_5068113"><td height="1">
<div align="right"></div></td></tr></tbody></table><div align="center">
<hr width="760" color="#99CC00" size="1"></div></body></html>
    `;
  }
  public render() {
    const { drama, theme } = this.state;
    return (
      <Fragment>
        <SEO />
        <Screen id="screen" srcDoc={this.previewCode(drama, theme)} />
      </Fragment>
    );
  }
}

export const ThemeQuery = graphql`
  query($id: String!) {
    themeYaml(id: { eq: $id }) {
      author {
        name
      }
      id
      name
      tags
      cover
      css
    }
  }
`;
